import {
	request
} from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			goods:[],
			comment:[
				{
					imgs:[],
				}
			],
			order_id:'',
			order_no:'',
			$src: this.$src
		};
	},
	onLoad(options){
		let id = options.id;
		this.getInfo(id);
	},
	methods:{
		getInfo(id){
			let that = this;
			request({
				url: 'index.php?s=/wap/order/apiReviewCommodity',
				data: {
					order_id: id
				},
			}).then(function(res){
				that.goods = res.data.data.list;
				let comment = [];
				for(let i=0;i<that.goods.length;i++){
					let temp = {
						'is_anonymous':false,
						'scores':5,
						'explain_type':1,
						'order_id':res.data.data.order_id,
						'order_goods_id':that.goods[i].order_goods_id,
						'goods_id':that.goods[i].goods_id,
						'content':'',
						'imgs':[],
						};
					comment[i] = temp;
				}
				that.comment = comment;
				that.order_id = res.data.data.order_id;
				that.order_no = res.data.data.order_no;
			});
		},
		changeContent(obj){
			console.log(obj);
			console.log(obj.target.dataset.index);
			this.comment[obj.target.dataset.index]['content'] = obj.detail.value;
		},
		uploadImage(i){
			let that = this;
			let comment = that.comment;
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        uni.uploadFile({
						url:that.$api+"index.php?s=/wap/upload/uploadfile",
						filePath:res.tempFilePaths[0],
						name:'file_upload',
						formData: {
						    file_path:'upload/common/',
						},
						success:function(res){
							let data = JSON.parse(res.data);
							console.log(data);
							if(data.code==1){
								comment[i]['imgs'].push(data.data);
								that.comment = comment;
							}
						}
					});
			    }
			});
		},
		submitSend(){
			let that = this;
			let comment = that.comment;
			console.log(comment);
			request({
				url: 'index.php?s=/wap/order/ApiAddGoodsEvaluate',
				data: {
					order_id: that.order_id,
					order_no:that.order_no,
					goodsEvaluate:JSON.stringify(comment),
				},
				method:"POST",
			}).then(function(res){
				uni.showToast({
					title:res.data.msg,
					duration:2000,
					icon: 'none'
				})
				if(res.data.code==1){
					setTimeout(function(){
						uni.navigateBack({
							delta:1
						})
					},2000);
				}
			});
		}
	},
}
